import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import HospitalAdmission from '../QrCode/QrCode';
import {getUser,getTicketCatagory,getHospital,getDepartment} from './../Apis.js'
//import usersData from './UsersData'

class Ward extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      showHospitalInfo:false
    };
  }

  componentDidMount() {
    this.getTicketCatagory(this.props.match.params.id)
  
  }
  getTicketCatagory=async(id)=>{
    const responseData= await getTicketCatagory(id);
    console.log(responseData)
      if(responseData.status == 200){
        this.setState({
           
          items: responseData.data
        });
        // this.getDepartment( responseData.data.department_id)
        this.getUser( responseData.data.ticketCategory.created_by,'creator')
        this.getUser( responseData.data.ticketCategory.modified_by,'Modifier')
        this.getHospital( responseData.data.ticketCategory.hospital_id)
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  getDepartment=async(id)=>{
    const responseData= await getDepartment(id);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          
          departName: responseData.data.name
            
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  showHospitalInfo=()=>{
    this.setState({showHospitalInfo:true})
  }
  getUser=async(id,user)=>{
    
    const responseData= await getUser(id);
    console.log(responseData,id,'response in creator')
      if(user=='creator')
      {
        if(responseData.status==200)
          {
            this.setState({
              isLoaded: true,
              creatorName: responseData.data.user.first_name+' '+ responseData.data.user.last_name
            });
           
         
        }
        else{
          this.setState({
            creatorName:'Creator has been removed'
        });
        }
       
      }
      else if(user=='Modifier'){
        
        if(responseData.status==200)
        {
          this.setState({
            modifierName: responseData.data.user.first_name+' '+ responseData.data.user.last_name
          });
        }
        else{
          this.setState({
            modifierName:'Not yet modified by any user'
          });
         
        }
      }
      
      
  }
  
  getHospital=async(id)=>{
    const responseData= await getHospital(id);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          hospitalName: responseData.data.name
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  render() {

    const ticketCatagory = this.state.items; //usersData.find( user => user.id.toString() === this.props.match.params.id)

    let roleName = '-';

    // if(user.roles !== undefined && user.roles.length !== 0){
    //   roleName = user.roles[0].name;
    // }

    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={3}>
          </Col>
          <Col lg={6}>
            <Card>
              <CardHeader>
                <strong><i className="icon-info pr-1"></i>Ticket Catagory id: {this.props.match.params.id}</strong>
                {/* <button onClick={this.showHospitalInfo} className='btn btn-success  userbtn'>Enter Admission Info</button> */}
              </CardHeader>
              <CardBody>
              {ticketCatagory.ticketCategory&&
                  <Table responsive striped hover>
                  <tbody>                    
                    <tr>
                      <td>Ticket Catagory Id :</td>
                      <td><strong>{ticketCatagory.ticketCategory&&ticketCatagory.ticketCategory.name}</strong></td>
                    </tr>
                
                    <tr>
                      <td>Color:</td>
                      <td><span style={{width:20,height:20,borderRadius:50,backgroundColor:ticketCatagory.ticket_category_priority[0].color,display:'inline-flex',position:'absolute',}}/><strong style={{marginLeft:28}}>{ticketCatagory.ticket_category_priority[0].color}</strong></td>
                    </tr>
                    <tr>
                      <td>Hospital Name:</td>
                      <td><strong>{this.state.hospitalName}</strong></td>
                    </tr>
                
              
                    <tr>
                      <td>Status:</td>
                      <td><strong>{ticketCatagory.status==1?'Enable':'Disable'}</strong></td>
                    </tr>
                   
                    <tr>
                      <td>Created By:</td>
                      <td><strong>{this.state.creatorName}</strong></td>
                    </tr>
                    <tr>
                      <td>Modified By:</td>
                      <td><strong>{this.state.modifierName}</strong></td>
                    </tr>
                
                  </tbody>
                </Table>}
              
                 
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default Ward;
